    &__options {
        max-width: 600px;
    }

    &__sort {
        margin: 0;
        padding: 0;
        list-style: none;

        &:empty {
            display: none !important;
        }
    }

    .slider-opt-list {
        margin: 0 0 15px;
        list-style: none;
        padding: 15px;
        background: #fff;
        box-shadow: 0 0 2px rgba(0, 0, 0, .05), 0 2px 4px rgba(0, 0, 0, .05);
        border: 1px solid #e6e6e6;
        border-radius: 2px;

        h4 {
            border-bottom: 1px solid #e6e6e6;
            padding-bottom: 5px;
            color: #333;
        }

        .accordion {
            margin-bottom: 15px;
        }
    }

    .slider-option {
        display: flex;
        margin-bottom: 10px;
        min-height: 30px;

        &__title {
            flex: 1 1 250px;
            display: flex !important;
            align-items: center;
            border-bottom: 1px dashed #e6e6e6;
            margin-right: 10px;
            padding-right: 20px;
            position: relative;
            color: #333;

            .question__wrap {
                right: 0;
            }
        }

        &:hover {
            input,
            .checkbox label,
            select {
                box-shadow: 0 0 2px 1px #0081ffa1 !important;
            }
        }

        &__value {
            margin: 0 !important;
            flex: 1 1 320px;

            select {
                margin-bottom: 0;
            }
        }
    }

    .preview-slide {
        &__wrap {
            position: relative;
            border: 1px solid #eaeaea;
            text-align: center;
        }

        &__empty {
            padding: 20px;
            display: block;
        }

        &__btns {
            position: absolute;
            right: 5px;
            top: 5px;
            z-index: 1;
        }
    }

    .slide-btns {
        display: flex;
        transition: .3s;
        opacity: .5;

        &:hover {
            opacity: 1;
        }

        &__item {
            color: #fff;
            width: 50px;
            height: 50px;
            padding: 10px;
            border: 1px solid #fff;
            border-radius: 2px;
            margin-right: 5px;
            background: rgba(0, 0, 0, 0.33);
            cursor: pointer;
            transition: .3s;

            &:hover {
                background: rgba(0, 0, 0, 0.6);
            }

            &_edit {
                i {
                    margin-right: -5px;
                }
            }
        }
    }

    .slides-sorter {
        display: flex;
        flex-wrap: wrap;
        padding: 10px;
        border-radius: 2px;
        border: 1px solid #eaeaea;
        margin-top: 10px;

        &__item {
            margin: 0 10px 10px 0;

            &_hidden {
                .slide-preview__img {
                    opacity: .5;
                }
            }

            &_active {
                outline: #2773eb auto 5px;
                outline-offset: 1px;
            }
        }
    }

    .slide-preview {
        cursor: grab;
        position: relative;
        justify-content: center;
        text-align: center;
        align-items: center;
        line-height: 1.4;
        text-overflow: ellipsis;
        overflow: hidden;

        &:after {
            content: "\f047";
            position: absolute;
            left: 0;
            top: 0;
            width: 100%;
            height: 100%;
            font-family: FontAwesome;
            display: flex;
            align-items: center;
            justify-content: center;
            visibility: hidden;
            opacity: 0;
            transition: .3s;
            font-size: 0;
            background: rgba(51, 51, 51, 0.35);
            color: #fff;
        }

        &:hover {
            &:after {
                opacity: 1;
                visibility: visible;
                font-size: 35px;
            }
        }

        &__img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            object-position: center;
        }

        &-ghost {
            width: 100px;
            height: 100px;
            background: rgba(51, 51, 51, 0.35);
            margin: 0 10px 10px 0;
            transition: .3s;
        }
    }

    .accordion {
        h4:not(:first-of-type) {
            margin-top: 25px;
        }

        &__wrap {
            & + .accordion__wrap {
                margin-top: -16px;
            }
        }

        .slider-opt-list {
            box-shadow: none;
            border: 0;
            padding: 0;
        }
    }

    &__shortcode {
        display: inline-block;
        margin: 0!important;
        padding: 3px 0!important;
        border-radius: 2px;
    }

    .slide-img-preview {
        margin-bottom: 15px;
    }

    .cke_editor_content {
        border: 1px solid #eaeaea !important;
        margin-bottom: 15px;
    }

    .slider-tabs {
        &__item {
            &_active {
                background: rgb(39, 115, 235)!important;
                color: #fff!important;

            }
        }
    }

    &__footer {
        position: sticky;
        bottom: 0;
        background: rgba(255, 255, 255, 0.88);
        z-index: 1;
        border-top: 1px solid #eaeaea;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: -1px;
    }

    .section__desc {
        background: #fff9d4;
    }
}


